import React from 'react';

import { ControlledInputCalendar, ControlledInputCalendarProps } from './ControlledInputCalendar';
import { useInputCalendar } from './useInputCalendar';

export type InputCalendarProps = Omit<ControlledInputCalendarProps, 'value' | 'onChange'> & {
  /** Выбранная дата */
  value?: Date;

  /** Колбэк изменения даты */
  onChange?: (value?: Date) => void;
};

export function InputCalendar({
  value,
  minDate,
  maxDate,
  onChange,
  ...rest
}: InputCalendarProps): JSX.Element {
  const [internalValue, setInternalValue] = useInputCalendar({
    value, minDate, maxDate, onChange
  });

  return (
    <ControlledInputCalendar
      { ...rest }
      value={ internalValue }
      minDate={ minDate }
      maxDate={ maxDate }
      onChange={ setInternalValue }
    />
  );
}
